<template>
  <div class="vip-card-title-container f-c">
    <img src="@/assets/images/radial_left.png" alt="">
    <div>{{ title }}</div>
    <img src="@/assets/images/radial_right.png" alt="">
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '无标题'
    }
  }
}
</script>

<style lang="scss" scoped>
.vip-card-title-container {
  width: 100%;
  
  img {
    width: 70px;
    height: 2px;
  }
  div {
    font-size: 18px;
    font-weight: bold;
    color: rgba(51,51,51,1);
    padding: 0 10px;
  }
}
</style>


