<template>
  <div class="vip-card-container">
    
    <div class="top-back" />
    
    <!-- // ? 信息和介绍区域 -->
    <div class="info-and-intro">
      <div v-if="userInfo" class="info">
        <div class="info-main flx">
          <div
            :style="{ 'background-image': `url( '${userInfo.avatar}' )` }"
            class="avatar"
          />
          <div class="content f-col-between">
            <p class="nickname one-ellipsis">
              {{ userInfo.nickName }}
            </p>
            <div class="time f-i-c">
              <img
                class="vip-icon"
                :src="userInfo.isMember ? vipYesIcon : vipNoIcon"
                alt=""
              >
              <p class="time-end">
                {{ userInfo.isMember ? `${userInfo.expTime}到期` : '未开通' }}
              </p>
            </div>
          </div>
          <div @click="buyMemberCard" class="renew-btn f-c">
            立即续费
          </div>
        </div>
      </div>
      
      <div class="chassis" />

      <div class="intro">
        <VipCardTitle title="会员福利介绍" />
        <div class="desc-img f-row-between">
          <img src="@/assets/images/vip_card_desc_1.png" alt="">
          <img src="@/assets/images/vip_card_desc_2.png" alt="">
          <img src="@/assets/images/vip_card_desc_3.png" alt="">
        </div>
      </div>
    </div>

    <div class="free-album">
      <VipCardTitle title="会员免费畅听" style="margin-bottom: 25px" />
      <div class="vip-card-modular">
        <div class="layout-item-box">
          <div
            v-for="album in vipFreeAlbumList"
            :key="album._id"
            @click="clickAlbum(album)"
            class="layout-item"
          >
              <VipCardModular
                :album="album"
              />
          </div>
        </div>
      </div>
      <div
        @click="toSeeMoreVipFree"
        class="more-free-btn"
      >
        更多畅听内容
      </div>
    </div>

    <div class="bottom-block-1" />

    <div v-if="membercard" class="buy-box flx">
      <div class="left f-c">
        ¥{{ membercard.price | fltToDecimal2 }}/年
      </div>
      <div @click="buyMemberCard" class="right f-c">
        立即续费
      </div>
    </div>

  </div>
</template>

<script>
import VipCardTitle from '@/components/vipCard/VipCardTitle.vue';
import { apiAlbumListByChargetype } from '@/service/api/program';
import VipCardModular from '@/components/vipCard/VipCardModular.vue';
import { apiOrderConfig, apiOrderPrePay, apiOrderSubmit } from '@/service/api/payment';
import { apiMembercard } from '@/service/api/program';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import { db } from '@/utils/lowdb';
import wx from 'weixin-js-sdk';
import Vue from 'vue';
import { Toast } from 'vant';/^[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}$/

Vue.use(Toast);

export default {
  components: {
    VipCardTitle,
    VipCardModular
  },

  data() {
    return {
      pageNum: 0,
      pageSize: 9,
      vipFreeAlbumList: [],
      
      membercard: null,
      
      userInfo: null,
      vipYesIcon: require('../../assets/images/my_vip_yes.png'),
      vipNoIcon: require('../../assets/images/my_vip_no.png')
    }
  },

  created() {
    document.title = '会员中心';
  },

  mounted() {
    this.getLocalUserInfo();
    this.getVipFreeAlbum();
    this.getMembercard();
  },

  methods: {
    // todo 获取本地存储中的用户信息
    getLocalUserInfo() {
      this.userInfo = db.get('userInfo').value();
    },

    buyMemberCard() {
      this.openNewOrder();
    },
    async openNewOrder() {
      let spus = [{
        spuId: this.membercard.spuId,
        skuId: '',
        quantity: '1',
        spuName: this.membercard.name
      }];

      const newOrderResponse = await apiOrderSubmit({ spus });
      if (!newOrderResponse || newOrderResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );

      this.launchWechatPay(newOrderResponse.data.orderNo);
    },
    async launchWechatPay(orderNumber) {
      const prePayResponse = await apiOrderPrePay( orderNumber );
      if (!prePayResponse || prePayResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );

      let wechatConfig = prePayResponse.data;
      wechatConfig.jsApiList = ['chooseWXPay'];
      wechatConfig.timestamp = wechatConfig.timeStamp;
      try {
        wx.config(wechatConfig);
      } catch (error) {
        console.log('支付配置 error: ', error);
      }

      wx.ready(function () {
        wx.chooseWXPay(wechatConfig);
      });
    },

    // todo 获取会员卡定义
    async getMembercard() {
      const memebercardResponse = await apiMembercard();
      if (!memebercardResponse || memebercardResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.membercard = memebercardResponse.data;
    },

    // todo 跳转至已购 - 会员畅听 tab
    toSeeMoreVipFree() {
      this.$router.push({
        name: 'purchased',
        query: {
          activeIndex: 1
        }
      })
    },

    // todo 获取会员免费的专辑
    async getVipFreeAlbum() {
      const vipFreeAlbumResponse = await apiAlbumListByChargetype('1', this.pageNum, this.pageSize);
      if (!vipFreeAlbumResponse || vipFreeAlbumResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.vipFreeAlbumList = vipFreeAlbumResponse.data;
    },

    // todo 跳转至专辑介绍页
    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  }
}
</script>

<style lang="scss" scoped>
.vip-card-container {
  width: 100%;
  padding-top: 20px;

  .top-back {
    width: 100%;
    height: 160px;
    background: linear-gradient(180deg,rgba(238,205,150,1),rgba(223,187,126,1));
    position: absolute;
    top: 0;
    left: 0;
  }

  .info-and-intro {
    width: calc(100% - 27px);
    margin: 0 13.5px;
    position: relative;

    .info {
      width: calc(100% - 28px);
      height: 110px;
      margin: 0 14px;
      padding: 35px 15px;
      box-sizing: border-box;
      background-image: url('../../assets/images/vip_card_info_back.png');
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      position: relative;
      top: 10px;

      .info-main {
        width: 100%;
        position: relative;

        .avatar {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-position: center center;
          background-size: cover;
          background-repeat: no-repeat;
        }
        .content {
          width: calc(100% - 140px);
          margin-left: 10px;

          .nickname {
            width: 100%;
            font-size: 18px;
            font-weight: bold;
            color: rgba(251,245,245,1);
          }
          .time {
            width: 100%;

            .vip-icon {
              width: 20px;
              height: 12px;
            }
            .time-end {
              margin-left: 5px;
              width: calc(100% - 25px);
              font-size: 13px;
              color: rgba(251,245,245,1);
            }
          }
        }
        .renew-btn {
          width: 90px;
          height: 33px;
          position: absolute;
          right: 0;
          top: 5px;
          background: rgba(244,186,65,1);
          box-shadow: 0px 0px 7px 1px rgba(0, 0, 0, 0.2);
          border-radius: 17px;
        }
      }
    }

    .chassis {
      width: calc(100% - 10px);
      margin: 0 5px;
      height: 3px;
      position: relative;
      background: #D5BB85;
      top: 8px;
      border-radius: 8px;
    }

    .intro {
      width: 100%;
      background-color: #fff;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      padding: 30px 14px 25px;

      .desc-img {
        margin-top: 25px;

        img {
          width: 98px;
          height: 120px;
        }
      }
    }
  }

  .free-album {
    width: 100%;
    padding: 25px 16px 30px;
    background-color: #fff;
    border-radius: 5px;
    margin-top: 25px;

    .vip-card-modular {
      width: 100%;
      box-sizing: border-box;
      padding: 0 0 13px;

      .title-row {
        width: 100%;
        box-sizing: border-box;
        padding: 0 16px;
      }
      .layout-item-box {
        width: 100%;
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        
        .layout-item {
          width: 33.3%;
          padding: 0 7px;
          margin-bottom: 20px;
        }
        .split-div-layout {
          display: none;
        }
      }
    }

    .more-free-btn {
      text-align: center;
      width: calc(100% - 200px);
      height: 44px;
      line-height: 44px;
      margin: 0 100px;
      background: rgba(255,255,255,1);
      border: 1px solid rgba(213,187,133,1);
      border-radius: 22px;
      font-size: 16px;
      font-weight: 600;
      color: rgba(213,187,133,1);
    }
  }

  .bottom-block-1 {
    width: 100%;
    height: 60px;
  }

  .buy-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 60px;
    background: rgba(255,255,255,1);
    border: 1px solid rgba(238,238,238,1);
    padding: 10px 20px;
    font-size: 18px;
    font-weight: bold;

    .left, .right {
      width: 50%;
    }
    .left {
      width: 50%;
      background: linear-gradient(-90deg,rgba(101,98,99,1) 16%,rgba(55,53,54,1) 100%);
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px;
      color: #fff;
    }
    .right {
      width: 50%;
      background: rgba(244,186,65,1);
      border-radius: 0 20px 20px 0;
    }
  }
}
</style>


