<template>
  <div v-if="album" class="base-title-cover-container">
    <div class="cover-region">
      <div
        class="back-cover"
        :style="{ 'background-image': `url( ${album.cover} )` }"
      />
      <p
        :class="['update', false ? 'hot' : '']"
      >
        <span v-if="album.isEndUpdate">
          {{ album.totalItemCount }}节全
        </span>
        <span v-else>
          更新至{{ album.itemCount }}节
        </span>
      </p>
      <p v-if="album.lastPlayTime" class="recent">
        {{ album.lastPlayTime | fltTimeInterval }}听过
      </p>
    </div>
    <p class="album-title two-ellipsis">{{ album.name }}</p>
  </div>
</template>

<script>
export default {
  props: {
    album: {
      type: Object,
      default: () => null
    }
  },

  data() {
    return {

    }
  }
}
</script>

<style lang="scss" scoped>
.base-title-cover-container {
  width: 100%;
  position: relative;
  
  .cover-region {
    width: 100%;
    height: 140px;
    position: relative;

    .back-cover {
      box-shadow: 0 0 1px #dfdfdf inset;
      width: 100%;
      height: 140px;
      border-radius: 5px;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
    }
    .update {
      position: absolute;
      right: 0;
      top: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
    .hot {
      background: rgba(214,66,66,1);
    }
    .recent {
      position: absolute;
      left: 0;
      bottom: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
  }
  .album-title {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
    font-size: 14px;
    color: rgba(34,34,34,1);
    line-height: 1.2;
    padding: 0 4.5px;
  }
  .had-hear {
    margin-top: 8px;
    font-size: 12px;
    color: rgba(187,187,187,1);
    padding: 0 4.5px;
    width: 100%;
    box-sizing: border-box;
  }
}
</style>


